<template>
    <Layout>
        <template v-slot:header>
            <h1 class="order-title">我的订单</h1>
            <nav class="order_nav" v-if="userPhone">
                <router-link class="order_nav-item"
                    :to="`/order/${nav}`"
                    v-for="(nav, index) in orderNavs" :key="index"
                    active-class="order_nav-item--active"
                >
                    {{nav}}
                </router-link>
            </nav>
        </template>
        <div class="order" v-if="userPhone">
            <keep-alive>
                <router-view :key="$route.fullPath" :orders="ordersFilter"></router-view>
            </keep-alive>
        </div>
        <NotLogin v-else/>
    </Layout>
</template>

<script>
import axios from 'axios'
import Layout from '@/components/layout/Layout.vue'
import NotLogin from '@/components/base/NotLogin.vue'
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
import sesstionStorage from '../../utils/sesstionStorage.js'

export default {
    name: 'Order',
    components: {
        Layout,
        NotLogin
    },
    data() { 
        return {
            orderNavs: ['全部', '待付款', '待收货', '已完成', '已取消'],
        }
    },
    computed: {
        ...mapState('login',{
            userPhone: state => state.userPhone,
        }),
        ...mapState('order',{
            orderState: state => state.orderState,
        }),
        ...mapGetters('order', [
            'ordersFilter'
        ])
    },
    methods: {
        ...mapActions('login', [
            'getLocalUphone',
        ]),
        ...mapMutations('order', [
            'getOrderState',
            'setOrderState'
        ]),
        ...mapActions('order', [
            'getOrders',
        ]),
        handleScoll(to, from){
            let scrollbarElement = document.querySelector('main.main')
            let initPositions = {}
            const pathPrefixReg = /^\/order\//
            let positions = sesstionStorage.getSession('orderPositions')
            const savePosition = () => {
                positions[from.params.orderState] = this.$store.state.scroll.scrollTop
                sesstionStorage.setSession('orderPositions', positions)
            }
            // 初始化
            this.orderNavs.map(orderState => initPositions[orderState] = 0)
            if(!positions){
                sesstionStorage.setSession('orderPositions', initPositions)
                positions = sesstionStorage.getSession('orderPositions')
            }
            // 进入 => 滚动
            if(!pathPrefixReg.test(from.fullPath) && pathPrefixReg.test(to.fullPath)){
                this.$nextTick(() => scrollbarElement.scrollTop = positions[this.orderState])
            }
            // 离开 => 保存 position
            if(pathPrefixReg.test(from.fullPath) && !pathPrefixReg.test(to.fullPath)){
                savePosition()
            }
            // 当前路由切换 => 保存 position + 滚动
            if(pathPrefixReg.test(from.fullPath) && pathPrefixReg.test(to.fullPath)){
                savePosition()
                this.$nextTick(() => scrollbarElement.scrollTop = positions[this.orderState])
            }
            
        }
    },
    watch: {
        '$route': 'handleScoll'
    },
    beforeRouteEnter (to, from, next) {
        next(vm => {
            if(from.fullPath === '/login/inputAuthcode' || from.fullPath === '/login'){
                vm.getLocalUphone()
            }
            // 获取 orderState by sesstionStorage
            vm.getOrderState()
            // '/order' 重定向
            // if(to.fullPath === '/order'){
            //     vm.$router.push(`/order/${vm.orderState}`)
            // }
        })
    },
    beforeRouteUpdate (to, from, next) {
        // orderState 赋值
        this.setOrderState(to.params.orderState)
        next()
    },
    beforeRouteLeave (to, from, next) {
        // 存储 orderState
        sesstionStorage.setSession('orderState', this.orderState)
        next()
    },
    created() {
        this.getLocalUphone()
        this.getOrders()
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

// * title
.order-title{
    text-align: center;
    line-height: 30px;
}
// * nav
.order_nav{
    .flex-sb-center();
    line-height: 1em;
    .padding-horizontal(@padding-base);
    background-color: @bg-white;
    .font-size-rem(@font-size-sm);
    border-bottom: 1px solid @border-color-divition;
    &-item{
        .padding-vertical(@padding-base);
        .padding-horizontal(10px);
        position: relative;
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 100%;
            width: 0;
            height: 2px;
            border-radius: 1px;
            background-color: @color-primary;
            transition: width  2s;
            transition: none;
        }
        &--active{
            .font-size-rem(@font-size-base, bold);
            color: #000;
            &::after{
                width: 100%;
                transition: all  .2s;
                left: 0;
                
            }
        }
        &--active ~ &::after{
            left: 0;
        }
    }
}
// * order
.order{
    height: 100%;
    &-list{
        &-item{
            .margin-top-rem(@margin-base);
            &:first-of-type{
                margin-top: 0;
            }
            &-header,
            &-content .goods-details,
            &-content .purchase_info,
            &-footer{
                .padding-horizontal(@padding-base);
            }
            // header
            &-header{
                background-color: @bg-white;
                .padding-vertical(@padding-sm);
                &>a{
                    .flex-sb-center();
                    .store-image{
                        .flex-shrink(0);
                        .image-wrapper(20px);
                    }
                    .store-name{
                        .flex-grow(1);
                        .flex-grow(1);
                        margin-left: 10px;
                        .font-size-rem(@font-size-base, bold);
                        color: #000;
                    }
                    .completion-status{
                        .font-size-rem(@font-size-base);
                    }
                }
            }
            // content
            &-content{
                .goods-details{
                    @image-height: 50px;
                    .flex();
                    .padding-vertical(@padding-base);
                    align-items: center;
                    background-color: #f2f6fc;
                    &-image{
                        .flex-shrink(0);
                        .image-bg(@image-height)
                    }
                    &-name{
                        .flex-grow(1);
                        margin-left: 10px;
                        padding-right: 20px;
                        line-height: 1.2em;
                        color: #000;
                        .font-size-rem(14px);
                        max-height: @image-height;
                        overflow: hidden;
                    }
                }
                .purchase_info{
                    background-color: @bg-white;
                    .padding-vertical(@padding-sm);
                    &-details{
                        float: right;
                        .font-size-rem(14px);
                        color: #000;
                        .price{
                            .font-size-rem(16px, bold);
                        }
                    }
                }
            }
            // footer
            &-footer{
                border-top: 1px solid @border-color-divition;
                text-align: right;
                background-color: @bg-white;
                .padding-vertical(@padding-sm);
            }
        }
    }
}
</style>